@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
.about .about__text {
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 20px;
}

.about {
  display: flex;
  align-items: center;
  justify-self: center;
}

.navigation {
  background-color: white;
  position: fixed;
  display: flex;
  align-items: center;
  flex-direction: column;
  left: 10px;
  top: 30px;
  padding: 2px 8px;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  color: steelblue;
  font-weight: 300;
  border-radius: 5px;
  box-shadow: 3px 4px 5px rgba(0, 0, 0, 0.1);
}

.navigation .nav__home,
.navigation .nav__about {
  padding: 5px 10px;
  color: inherit;
  text-decoration: none;
  letter-spacing: 1px;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 80%;
}

.movies .movie__link {
  display: contents;
  text-decoration: none;
}

.movies .movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.3);
  min-width: 550px;
}

.movie img {
  position: relative;
  top: -30px;
  max-width: 180px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.3);
}

.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}

.movie__genres li {
  margin-right: 10px;
  font-size: 14px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Muli", sans-serif;
  background-color: #eff3f7;
  height: 100%;
}

html,
body,
#root,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 1000;
}

